import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Ideia_reuniao_pic } from '../../../modelo/Ideia_reuniao_pic'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'ideia_reuniao_pic-dados-edit',   templateUrl: 'ideia_reuniao_pic_dadosEdit.component.html' }) export class Ideia_reuniao_picDadosEditComponent implements OnInit {    ideia_reuniao_pic: Ideia_reuniao_pic;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.ideia_reuniao_pic.Ideia_reuniao_pic";                 let id = +params['id'];         this.ideia_reuniao_pic = new Ideia_reuniao_pic();         this.ideia_reuniao_pic.cd_ideia_reuniao_pic = id;         o.objetoJson = this.ideia_reuniao_pic;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a ideia_reuniao_pic. ';       }      this.ideia_reuniao_pic = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


